#navigation a {
  color: white;
  text-decoration: none;
  margin: 20px;
}
#navigation img {
  height: 30px;
}

#home {
  height: 800px;
  background-image: url("images/gramophone_ivory.jpg");
  background-position: right top;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #161b1e;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#home #roxylogo {
  background: radial-gradient(
    ellipse at center,
    rgba(0, 0, 0, 0.65) 0%,
    rgba(0, 0, 0, 0.65) 46%,
    rgba(0, 0, 0, 0) 100%
  );
  border-radius: 15px;
  animation: draw 3s 1 forwards normal;
  padding: 20px 10px;
  height: 230px;
  width: 370px;
}
#home .flexContainer {
  display: flex;
  justify-content: center;
}
#home #restaurantGuru {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  position: relative;
  top: 70px;
  padding-right: 25px;
  opacity: 95%;
}
#home h6 {
  color: white;
  font-size: 24px;
  position: relative;
  top: 70px;
  text-align: center;
}
#home a {
  text-decoration: none;
  color: white;
}
@keyframes draw {
  0% {
    fill-opacity: 0;
    stroke-opacity: 0;
  }
  100% {
    fill-opacity: 1;
    stroke-opacity: 1;
  }
}

#openForMusic {
  background-image: url("images/wood_texture.jpg");
  background-size: cover;
  padding-bottom: 75px;
  padding-top: 25px;
}
#openForMusic .flex-container {
  display: flex;
  justify-content: center;
}
#openForMusic img {
  width: 40%;
  height: 100%;
  margin: 20px auto;
  align-self: center;
}
#openForMusic p {
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 25px;
  margin: 25px 150px;
  text-align: center;
  background: radial-gradient(
    ellipse at center,
    rgba(0, 0, 0, 0.2) 0%,
    rgba(0, 0, 0, 0.2) 46%,
    rgba(0, 0, 0, 0) 100%
  );
}
#eventcal {
  text-align: center;
  background-color: rgba(0, 0, 0, 0.048);
}

.calendar {
  margin-bottom: 60px;
}

#musiccal {
  background-color: rgba(0, 0, 0, 0.048);
  text-align: center;
}

#menu header {
  background-color: #212121;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
#menu img {
  height: 60%;
}
#menu p {
  text-align: center;
}
#menu .flex-container {
  display: flex;
  justify-content: center;
}
#menu .toastButton {
  margin-top: 30px;
}

#foodmenu {
  background-color: gray;
}
#foodmenu p {
  text-align: center !important;
}

#drinks,
#brunch,
#smallPlates,
#dinner,
#specials {
  padding-bottom: 100px;
  text-align: center;
}
#drinks h3,
#brunch h3,
#smallPlates h3,
#dinner h3 {
  text-align: center;
  background-color: rgba(0, 0, 0, 0.048);
}
#drinks .cocktailList,
#drinks .brunchList,
#drinks .plateList,
#drinks .dinnerList,
#brunch .cocktailList,
#brunch .brunchList,
#brunch .plateList,
#brunch .dinnerList,
#smallPlates .cocktailList,
#smallPlates .brunchList,
#smallPlates .plateList,
#smallPlates .dinnerList,
#dinner .cocktailList,
#dinner .brunchList,
#dinner .plateList,
#dinner .dinnerList {
  margin: auto 250px;
}
#drinks p,
#brunch p,
#smallPlates p,
#dinner p {
  font-size: 22px;
  margin-bottom: 2px;
}
#drinks span,
#brunch span,
#smallPlates span,
#dinner span {
  font-size: 13px;
  color: gray;
  font-style: italic;
}

#contact {
  color: white;
  background-image: url("images/barrels.jpg");
  background-size: cover;
  padding-bottom: 100px;
  padding-top: 1px;
}
#contact header {
  background-color: #212121;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
#contact img {
  height: 60%;
}
#contact h2 {
  text-align: center;
  background-color: rgba(0, 0, 0, 0.322);
  padding: 5px;
}
#contact p {
  margin: 0px;
}
#contact a {
  text-decoration: none;
  color: white;
}
#contact .flexContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
#contact .contacts {
  display: flex;
  flex-direction: column;
}
#contact .contactTitle {
  font-weight: bold;
}
#contact .emailLink:hover {
  color: gray;
}
#contact .contactLink {
  color: white;
  text-decoration: none;
}
#contact div.info {
  font-size: 14px;
  margin: 20px 100px;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 20px;
  border-radius: 8px;
}
#contact iframe {
  width: 80%;
  height: 300px;
  margin: 0 10%;
  border: 0;
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
}
#contact button {
  background-color: #c9302c;
  border: 1px solid #ac2925;
  margin-left: 5px;
  padding: 6px 12px;
  font-size: 14px;
  border-radius: 4px;
  color: white;
}
#contact button:hover {
  background-color: #ac2925;
  border: 1px solid #ac2925;
}

#calendars .calendar {
  margin-bottom: 60px;
}

h1 {
  font-size: 30px;
  font-weight: bold;
}
h2 {
  font-size: 26px;
  font-weight: bold;
}
h3 {
  font-size: 22px;
  font-weight: bold;
}
.flexContainer {
  display: flex;
  justify-content: center;
}

.musicEventDiv {
  text-align: center;
  margin: 10px;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  border: 2px solid gray;
  background-color: rgba(0, 0, 0, 0.048);
  border-radius: 5px;
  padding: 10px;
}

.eventEventDiv {
  text-align: center;
  margin: 10px;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  border: 2px solid gray;
  background-color: rgba(0, 0, 0, 0.048);
  border-radius: 5px;
  padding: 10px;
}

div.rbc-event,
div.rbc-event.rbc-selected {
  background-color: #642930;
}

div.rbc-event {
  color: white;
  font-size: 12px;
}

.rbc-toolbar span.rbc-toolbar-label {
  font-size: 18px;
}

@media only screen and (max-width: 400px) {
  #calendars {
    display: none;
  }
  #calendars div.App.calendar:nth-child(2) div.rbc-calendar {
    display: none;
  }
}
@media only screen and (max-width: 411px) {
  #calendars {
    display: none;
  }
  #calendars div.App.calendar {
    display: none;
  }
  #calendars div.rbc-calendar {
    display: none;
  }
  #calendars div.rbc-month-view {
    display: none;
  }
  #calendars div.rbc-month-row,
  #calendars div.rbc-row-bg,
  #calendars div.rbc-row-content {
    display: none;
  }
  #calendars .rbc-toolbar button {
    display: none;
  }
}

@media only screen and (max-width: 425px) {
  #navigation a {
    font-size: 12px;
    margin: 7px;
  }

  #home {
    height: 500px;
  }
  #home h6 {
    font-size: 18px;
    margin: 0px 50px;
  }

  #openForMusic {
    padding-bottom: 50px;
    padding-top: 25px;
  }
  #openForMusic p {
    font-size: 20px;
    margin: 20px;
  }
  #openForMusic img {
    width: 80%;
  }
  #openForMusic h6 {
    color: white;
    margin: 0px 50px;
  }

  #contact .flexContainer {
    flex-direction: column;
  }
  #contact .info {
    margin: 20px;
    background-color: rgba(0, 0, 0, 0.7);
    padding: 15px;
    border-radius: 8px;
  }
  #contact iframe {
    width: 95%;
    margin: 0 2.5%;
  }

  #loader svg {
    padding: 50px;
  }

  div.musicEventDiv {
    max-width: 200px;
  }
  div.eventEventDiv {
    max-width: 200px;
  }

  #shop .checkout-state {
    width: 50%;
    max-width: 150px;
  }
}
@media only screen and (max-width: 360px) {
  #navigation a {
    font-size: 10px;
    margin: 5px;
  }

  #loader svg {
    padding: 25px;
  }
}

@media only screen and (max-width: 320px) {
  #navigation a {
    font-size: 8px;
    margin: 3px;
  }

  #loader svg {
    padding: 15px;
  }
}

#shop .shop-logo {
  height: 100px;
}
#shop .App-header {
  background-color: #212121;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
#shop .App-header-text {
  color: white;
}
#shop .App-shop {
  text-align: center;
}
#shop .App-products {
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 800px) {
  #shop .App-products {
    grid-template-columns: 1fr 1fr;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#shop .Product {
  margin: 1rem auto;
  max-width: 300px;
  border: 1px solid #ccc;
}
#shop .Product img {
  width: 100%;
}
@media (min-width: 800px) {
  #shop .Product {
    margin: 1rem;
  }
}

#shop .Product-title {
  font-size: 1.2rem;
}
#shop .Product-price {
  font-size: 1rem;
}
#shop .Product-buy-button {
  display: inline-block;
  margin: 0 0 1rem 0;
  padding: 0.85em 1em;
  border: 0;
  outline: 0;
  border-radius: 100em;
  font-size: 0.9rem;
  font-weight: 600;
  line-height: 1;
  text-align: center;
  background-color: #642930;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.25s ease-out;
  -webkit-appearance: none;
}
#shop .Product-buy-button:hover,
#shop .Product-buy-button:focus {
  background-color: #642930da;
}
#shop .Cart {
  position: fixed;
  top: 20px;
  right: 20px;
  width: 350px;
  background: white;
  z-index: 100;
  border: 3px solid #642930;
}
#shop .Cart-total-cost {
  color: black;
  text-align: right;
}
#shop .CartItem {
  border-bottom: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
#shop .CartItem-details {
  flex: 1;
}
#shop .CartItem-details div {
  margin-bottom: 0.25rem;
}
#shop .CartItem-quantity {
  margin-left: 1rem;
}
#shop .delete-item {
  margin-left: 1rem;
}
#shop .CheckoutForm {
  border: 1px solid #ccc;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  max-width: 600px;
  margin: 0 auto;
  padding: 1rem;
  text-align: center;
}
#shop .CheckoutForm-complete {
  text-align: center;
}
#shop .CheckoutForm-button {
  display: inline-block;
  margin: 0 0 1rem 0;
  padding: 0.85em 1em;
  border: 0;
  outline: 0;
  border-radius: 100em;
  font-size: 0.9rem;
  font-weight: 600;
  line-height: 1;
  text-align: center;
  background-color: #642930;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.25s ease-out;
  -webkit-appearance: none;
}
#shop .CheckoutForm-button:hover,
#shop .CheckoutForm-button:focus {
  background-color: #642930da;
}
#shop .CheckoutForm-button:disabled {
  background-color: #ccc;
}
#shop .CheckoutForm-error {
  color: #dc4545;
}
#shop .StripeElement {
  display: block;
  margin: 0.5rem auto 1.5rem;
  max-width: 500px;
  padding: 12px 16px;
  font-size: 1rem;
  border: 1px solid #eee;
  border-radius: 3px;
  outline: 0;
  background: white;
}
#shop .checkout-input {
  width: 100%;
  max-width: 500px;
  padding: 12px 16px;
  margin: 8px;
  font-size: 1rem;
  border: 1px solid #eee;
  border-radius: 3px;
  outline: 0;
  background: white;
}
#shop .checkout-city {
  width: 50%;
  max-width: 242px;
  padding: 12px 16px;
  margin: 8px;
  font-size: 1rem;
  border: 1px solid #eee;
  border-radius: 3px;
  outline: 0;
  background: white;
}
#shop .checkout-state {
  width: 10%;
  max-width: 75px;
  padding: 12px 16px;
  margin: 8px;
  font-size: 1rem;
  border: 1px solid #eee;
  border-radius: 3px;
  outline: 0;
  background: white;
}
#shop .checkout-zip {
  width: 40%;
  max-width: 150px;
  padding: 12px 16px;
  margin: 8px;
  font-size: 1rem;
  border: 1px solid #eee;
  border-radius: 3px;
  outline: 0;
  background: white;
}

#loader {
  height: 600px;
  display: flex;
  justify-content: center;
}
#loader svg {
  height: 70%;
  margin-top: 50px;
  padding: 150px;
  animation: loaderFadeOut 4.5s 1 forwards;
}
#loader .orb path {
  color: gray;
  stroke: gray;
  fill: gray;
  animation: loader 1.4s infinite forwards;
}
@keyframes loader {
  0% {
    transform: rotate(0deg);
    transform-origin: 59.2px 59.2px;
  }
  100% {
    transform: rotate(360deg);
    transform-origin: 59.2px 59.2px;
  }
}
@keyframes loaderFadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

#insta .App-header {
  background-color: #212121;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
#insta img {
  height: 60%;
}

#instagramFeed {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
#instagramFeed div.posts {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#instagramFeed img {
  width: 350px;
  height: 350px;
  padding: 5px;
}

* {
  font-family: "Lato", sans-serif !important;
}

html {
  scroll-behavior: smooth;
}
